import $ from 'jquery';
import 'theme_boost_vorsprung/bootstrap/popover';
import 'theme_boost_vorsprung/bootstrap/tooltip';

export const init = () => {
  $(function(){
    // TODOJH: Remove
    // alert('initjquery.js');
    // console.log('Entered initjquery.js');
    $('.dropdown-menu>a').on('click', function(){
      $('.navbar-collapse').collapse('hide');
    });
    $('.autohidepopover').popover().click(function () {
      setTimeout(function () {
        $('.autohidepopover').popover('hide');
      }, 2000);
    });
    $('.hoverpopover').popover({
      trigger: 'hover'
    }).on('click', function(e) {e.preventDefault(); return true;});

    $('.clickpopover').popover();
    $('[data-toggle="tooltip"]').tooltip();
  });
};

